        background-color: #181a27;
    }

    .navbar-nav .nav-item a:after,
    .navbar-nav .nav-item a:active:after {
        visibility: hidden;
    }

    .brand {
        margin-top: 10px;
        color: #d985fa;
    }

    .nav-link {
        padding: 0.7rem 1rem;
    }

    .container button {
