    padding-left: 1rem;
}

.nav-link {
    padding: 0.8rem 1rem;
}

.navbar-nav .nav-item {
    position: relative;
    margin-left: 20px;
    margin-left: 25px;
}

.navbar-nav .nav-item a {
    font-weight: 400;
    transition: all 0.3s ease-out 0s;
    position: relative;
    z-index: 1;
}

.navbar-nav .nav-item:nth-child(-n+4) a::after {
    content: "";
    position: relative;
    display: block;
    height: 5px;
    width: 0;
    top: 2px;
    border-radius: 16px;
    background: #c95bf5;
    bottom: 1px;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease-in-out 0s;
}

.navbar-nav .nav-item a:hover::after {
    width: 100%;
}

